.card-saved {
    display: inline-block;
    position: relative;
    width: 70%;
    min-width: 250px;
    height: 500px;
    background-color: #eff4f5;
    border: 1px solid var(--color-terciary);
    border-radius: 5px;

    .card__img-saved {
        width: 100%;
        height: 65%;
        object-fit: cover;
        margin: auto;
        border-radius: 5px 5px 0px 0px;
    }

    .card__data-saved {
        width: 100%;
        height: 26%;
        word-wrap: break-word;
        overflow: hidden;

        .card__info {
            display: flex;
            gap: 5px;
            margin: 0px 5px;
        }

        .card__info-saved {
            display: flex;
            flex-direction: column;
            width: 80%;
            gap: 5px;
        }

        .card__likes-saved {
            display: flex;
            width: 20%;
            flex-direction: column;
            align-items: center;
            padding: 3px 10px;
            background-color: #D90429cc;
            border-radius: 5px;

            .likes__icon-saved,
            .likes__num-saved {
                color: var(--color-terciary);
            }

            .likes__num-saved {
                font-family: var(--font-text);
                font-weight: 500;
                margin-left: 3px;
            }
        }

        .card__date-saved,
        .card__size-saved {
            display: inline-block;
            background-color: #2B2D42cc;
            color: var(--color-terciary);
            font-family: var(--font-text);
            font-weight: 500;
            padding: 3px 10px;
            border-radius: 5px;
        }

        .card__description-saved {
            padding: 10px;
            margin-right: 30px;
            font-family: var(--font-text);
            font-weight: 400;
            padding: 3px 10px;
            border-radius: 5px;
        }
    }

    .card__buttons {
        display: flex;
        justify-content: flex-end;

        .card__icon {
            color: var(--color-secondary);
        }
    }
}

.modal {
    .modal__container {
        display: flex;
        flex-direction: column;
        gap: 20px;
        width: 50%;
        padding: 40px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 400;
        background-color: white;
        border-radius: 5px;

        .modal__text {
            align-self: flex-start;
            font-family: var(--font-text);
            font-size: 1.2rem;
        }

        .modal__input {
            width: 100%;
        }

        .modal__button {
            background-color: var(--color-primary);
            color: var(--color-terciary);
            font-family: var(--font-text);
            font-weight: 600;
            text-decoration: none;
            padding: 10px 30px;
            border: none;
            border-radius: 5px;
            box-shadow: 0px 3px 6px var(--color-primary-shadow);
        }

        .modal__close {
            position: absolute;
            top: 5px;
            right: 5px;
            color: var(--color-secondary);
        }
    }
}

@media only screen and (min-width: 550px) {
    .card-saved {
        width: 49%;
    }
}


@media only screen and (min-width: 1000px) {
    .card-saved {
        width: 24%;
    }
}
